.wrapper {
  display: grid;
  grid-template-columns: 250px 1fr;
  grid-template-rows: auto auto 1fr;
  grid-template-areas: "img name" "img price" "img description";
  padding: 50px 100px;
  font-family: sans-serif; }
  .wrapper .img {
    grid-area: img;
    height: 400px; }
  .wrapper .name {
    grid-area: name;
    font-weight: bold;
    color: #a3a3a3; }
  .wrapper .price {
    grid-area: price; }
  .wrapper .description {
    grid-area: description;
    font-family: sans-serif;
    line-height: 1.7; }
  .wrapper .name, .wrapper .price, .wrapper .description {
    padding: 20px; }
